import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //记录点击的音乐
    id: '',
    //当前需要播放的 音乐
    musicIndex: 0,
    audioList: [],
    //播放或暂停
    isPlayState: false,
    //播放列表
    isMusicList:false
  },

  mutations: {
    //获取 传来的音乐的id 切换
    tapMusicId(state, obj) {
      //添加
      this.state.audioList.push(obj)
      let arr = this.state.audioList
      //去重
      let nArr = arr.filter((element, index, self) => {
        return self.findIndex(x => x.name === element.name) === index
      })
      //当前需要播放的 音乐
      nArr.forEach((item, index) => {
        if (item.id == obj.id) {
          this.state.musicIndex = index
          return
        }
      });
      this.state.audioList = nArr
      //播放状态
      this.state.isPlayState = obj.isPlayState
      console.log(this.state.isPlayState)
    },
    //弹出 播放列表
    showMusicList(state,flag){
      this.state.isMusicList = flag
    }
  },
  getters: {

  },
  actions: {

  },
  modules: {
  }
})
