<template>
  <div class="home">
    <div class="header">
      <div class="left">三</div>
      <div>
        <van-search
          class="ipt"
          v-model="value"
          placeholder="请输入搜索关键词"
        />
      </div>
      <div>
        <img class="yuying" src="../assets/image/icon/yuying.png" alt="" />
      </div>
    </div>
    <div class="carousel">
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(item, index) in banners" :key="index">
          <img v-lazy="item.imageUrl" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="nav">
      <ul>
        <li v-for="(item, index) in icon" :key="index">
          <div>
            <img :src="item.iconUrl" alt="" />
          </div>
          <div>
            {{ item.name }}
          </div>
        </li>
      </ul>
    </div>
    <!-- 推荐歌单 -->
    <div class="nominate">
      <div class="title">
        <span>推荐歌单</span>
        <van-button class="btn" color="#7232dd" round type="info"
          >更多></van-button
        >
      </div>
      <div class="song-list">
        <ul>
          <li
            class="songlist-item"
            v-for="(item, index) in songList"
            :key="index"
          >
            <div>
              <img :src="item.picUrl" alt="" />
            </div>
            <div>
              {{ item.name }}
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!-- 最新音乐 -->
    <div class="newmusic">
      <div class="title">
        <span>最新音乐</span>
        <button>播放</button>
      </div>
      <div class="musiclist">
        <div>
          <ul class="list" v-for="(item, index) in newSong" :key="index">
            <li
              class="item"
              v-for="(v, i) in item"
              :key="i"
              @click="musicId(v,index,i)"
            >
              <!-- //歌曲图片 -->
              <img :src="v.picUrl" alt="" />
              <div class="content">
                <!-- 歌曲名字 -->
                <h5>{{ v.name }}</h5>
                <!-- //歌手 -->
                <div v-if="v.song">
                  <span v-for="(a, b) in v.song.artists" :key="b">
                    {{ a.name }} /
                  </span>
                </div>
              </div>
              <div class="right"></div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      //搜索输入的内容
      value: "",
      //轮播
      banners: [],
      // 图标
      icon: [],
      //显示推荐的歌单
      songList: [],
      //存放 全部的歌单
      personalized: [],
      //推荐的新音乐
      newSong: [],
      //歌词
      lrc: "",
      //当前点击的歌曲id
      id: ""
    };
  },

  methods: {
    //获取轮播图
    async getBanner() {
      this.axios({
        url: "banner",
        data: {
          type: 1,
        },
      })
        .then((res) => {
          if (res.code == 200) {
            this.banners = res.banners;
          }
        })
        .catch((err) => {
          console.log("err==>", err);
        });
    },
    //获取图标
    async getIcon() {
      this.axios({
        url: "homepage/dragon/ball",
      })
        .then((res) => {
          if (res.code == 200) {
            this.icon = res.data;
          }
        })
        .catch((err) => {
          console.log("err==>", err);
        });
    },
    //获取推荐歌单
    async getPersonalized() {
      this.axios({
        url: "personalized",
        data: {
          limit: 6,
        },
      })
        .then((res) => {
          if (res.code == 200) {
            this.songList = res.result.slice(0, 9);
            this.personalized = res.result;
          }
        })
        .catch((err) => {
          console.log("err==>", err);
        });
    },
    //获取推荐新音乐
    async getNewSong() {
      this.axios({ url: "personalized/newsong" })
        .then((res) => {
          let arr = res;
          if (arr.code == 200) {
            //对请求到的数据进行处理
            let newArr = [];
            for (let i = 0; i < arr.result.length; i += 3) {
              //三组三组分割
              let arrs = [arr.result[i], arr.result[i + 1], arr.result[i + 2]];
              newArr.push(arrs)
            }
            //去除undefined
            newArr.forEach((item) => {
              item.forEach((v, i) => {
                if (v == undefined) {
                  item.splice(i, 2);
                }else{
                  //添加 一个 播放状态
                   v.isPlayState = false
                }
              });
            });
            this.newSong = newArr;
          }
        })
        .catch((err) => {
          console.log("err==>", err);
        });
    },
    //点击播放音乐 或暂停音乐,播放列表切换
    musicId(audio,index,i) {
      //播放与暂停状态的切换
      this.tapPlay(index,i)
      let obj = {};
      //获取歌词
      this.getLuric(audio.id);
      obj.lrc = this.lrc;
      //记录当前点击的 音乐id
      this.id = audio.id;
      // 歌曲id
      obj.id = audio.id;
      //歌曲名字
      obj.name = audio.name;
      // 歌手
      obj.artist = "";
      //歌曲播放地址
      obj.url = `https://music.163.com/song/media/outer/url?id=${audio.id}.mp3`;
      // 歌曲图片
      obj.cover = audio.picUrl;
      //播放状态
      obj.isPlayState = audio.isPlayState
      for (let i in audio.song.artists) {
        //获取歌手
        obj.artist += audio.song.artists[i].name + "/";
      }
      console.log(obj)
      this.$store.commit("tapMusicId", obj);
    },
    //歌词获取
    getLuric(id) {
      if (id == this.id) {
        return;
      }
      this.axios
        .get("lyric", {
          params: {
            id: id,
          },
        })
        .then((res) => {
          if (res.code == 200) {
            this.lrc = res.lrc.lyric;
          }
        });
    },
    //播放状态的切换
    tapPlay(index,i){
      let arr = this.newSong
      let flag =  arr[index][i].isPlayState
      //播放状态切换
      arr.forEach((v,i)=>{
        v.forEach(a=>{
          //都变为false
          a.isPlayState  = false
        })
      })
      this.flag = !this.flag
      //当前点击的 变为 false
      arr[index][i].isPlayState = !flag
      //重新赋值
      this.newSong = arr
    }
  },
  created() {
    //获取轮播
    this.getBanner();
    //获取图标
    this.getIcon();
    //获取推荐歌单
    this.getPersonalized();
    //获取推荐新音乐
    this.getNewSong();
  },
};
</script>

