import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path:'/podcast',
    name:'Podcast',
    component:()=>import('@/views/Podcast.vue')
  },
  {
    path:'/my',
    name:'My',
    component:()=>import('@/views/My.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
