<template>
  <div class="player">
    <div class="content">
      <div class="picImg">
        <div :class=" isPlayState ? '':'current' ">
          <img
            v-if="musicList[index].cover != ''"
            :src="musicList[index].cover"
          />
        </div>
      </div>
      <div class="music-name">
        <span class="name">{{ musicList[index].name }}</span
        >-
        <span class="artist">{{ musicList[index].artist }}</span>
      </div>
      <div class="play" @click="tagglePlay">
        <div>播放</div>
      </div>
      <div class="music-list-btn" @click="isMusicList">
        <img src="../assets/image/list.png" alt="" />
      </div>
    </div>
    <a-player
      ref="aplayer"
      class="audio"
      :audio="musicList[index]"
      :lrc-type="1"
    ></a-player>
    {{ audio }}
  </div>
</template>

<script>

export default {
  name: "play",
  props: {},
  data() {
    return {
      index: 0,
      musicList: [
        {
          name: "未选择",
          cover: "",
          url: "",
          artist: "未选择",
          lrc: "",
        },
      ],
    };
  },
  computed: {
    //获取 vuex 中的需要播放的音乐
    audio() {
      this.musicList = this.$store.state.audioList;
      this.index = this.$store.state.musicIndex;
      return;
    },
    //播放暂停
    isPlayState() {
      if(this.$store.state.isPlayState &&  this.$refs.aplayer){
        this.$refs.aplayer.play()
      }else if(this.$refs.aplayer){
        this.$refs.aplayer.pause()
      }
      return this.$store.state.isPlayState;
    },
    
  },
  watch: {},
  methods: {
    //播放或暂停
    tagglePlay(){
      this.$store.state.isPlayState = !this.$store.state.isPlayState
      console.log(this.$store.state.isPlayState)
      this.$refs.aplayer.toggle()
    },
    //播放列表弹出
    isMusicList(){
      this.$store.commit("showMusicList", true);
    }
  },
  watch: {},
  components: {
    
  },
};
</script>

<style lang="less" scoped>
.player {
  padding: 0;
  .content {
    width: 100%;
    height: 38px;
    background: white;
    border-top: 1px solid gainsboro;
    display: flex;
    justify-content: space-between;
    div {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    > div.picImg {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: black;
      text-align: center;
      position: relative;
      margin: -20px 5px 0 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      > div {
        width: 70%;
        height: 70%;
        border-radius: 50%;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        background: wheat;
        background-image: url("../assets/image/picUrl.png");
        background-size: 100%;
        // 设置动画
        animation-name: loop;
        animation-duration: 10s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;

        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
      .current {
        animation-play-state: paused;
      }
    }
    .music-name {
      width: 50%;
      height: 100%;
      line-height: 38px;
      .name {
        font-size: 17px;
      }
      .artist {
        color: gray;
      }
    }
    .play {
      width: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      > div {
        width: 70%;
        height: 70%;
        border-radius: 50%;
        text-align: center;
        line-height: calc(100% * 2);
        border-radius: 50%;
        border: 1px solid gainsboro;
        &:active {
          transition: all 0.5s;
          background: gray;
          transform: scale(2 , 2);
        }
      }
    }
    .music-list-btn {
      width: 10%;
      height: 100%;
      padding: 5px;
      img {
        width: 100%;
        height: 100%;
      }
    }

  }
}

// 定义动画
@keyframes loop {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.audio {
  display: none;
  width: 100%;
  height: 100%;
  position: relative;
  left: -5px;
}
</style>