import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import 'animate.css';
// 导入reset.css文件 
import './assets/css/reset.css'
// 导入animte.css文件（动画）
import './assets/css/animate.css'
// 导入web字体css文件
import './assets/webfont/webfont.css'
//移动端适应
import "lib-flexible/flexible"

import APlayer from '@moefe/vue-aplayer';

import './plugins/axios'

Vue.use(APlayer);

//vant组件导入
import {
  Button,
  Toast,
  Tabbar, TabbarItem,
  Swipe, SwipeItem,
  Search, Grid, GridItem,
  Lazyload, Popup, Tab, Tabs,
  Pagination
} from 'vant';
//使用组件
Vue.use(Button)
Vue.use(Toast)
Vue.use(Tabbar);
Vue.use(TabbarItem);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Search);
Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Lazyload);
Vue.use(Popup);
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(Pagination);


Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
